<template>
  <v-container>
    <div v-if="esDirecta">
      <h1>Evaluación</h1>
      <EvDirecta
        :esDirecta="esDirecta"
        :termref="termref"
        />
    </div>
    <!-- <div v-if="!esDirecta">
      <proponentesListaCorta
        :proponentes="proponentes"
        :evaluacion="true"
        :eliminar="false"
        @evaluarProp="evaluarProp($event)"
        >
      </proponentesListaCorta>
    </div> -->
    <div v-if="termref">
      <h2>Resultados</h2>
      <preResults :termref="termref" :proponentes="proponentes" @evaluarProp="evaluarProp($event)"/>
    </div>
  </v-container>
</template>
<script>
import proponentesListaCorta from '@/components/solProcContrata/proponentesListaCorta'
import EvDirecta from '@/components/evaluacion/EvDirecta'
import preResults from '@/components/solProcContrata/preResults'
import results from '@/components/solProcContrata/results'
import { termRef } from '@/servicios/TerminosReferencia.js'
export default {
  components: { proponentesListaCorta, EvDirecta, preResults, results },
  data () {
    return {
      proponentes: [],
      tipoContratacion: '',
      termref: ''
    }
  },
  async mounted () {
    const id = this.$route.params.idTermRef
    await this.getTermrefDetail(id)
  },
  methods: {
    async getTermrefDetail (id) {
      try {
        const { data } = await termRef.getTermRefDetail(id)
        console.log(data)
        this.setTermData(data)
        if (!this.esDirecta) {
          this.proponentes = data.proponentes
        }
      } catch (error) {
        console.log(error)
      }
    },
    evaluarProp (event) {
      const idTermRef = this.$route.params.idTermRef
      this.$router.push(`/Evaluacion/${idTermRef}/proponente/${event.id}`)
    },
    setTermData (data) {
      let obj = {
        id: data.id ? data.id : '',
        antecedentes: data.antecedentes,
        idSolContratacion: data.id_solcontratacion ? data.id_solcontratacion : this.idSolicitud,
        objContractual: data.obj_contractual ? data.obj_contractual : '',
        obligaciones: data.obligaciones ? data.obligaciones : [],
        infoprod: data.infoprod ? data.infoprod : [],
        fechaLimite: data.fecha_limite ? (data.fecha_limite === '0000-00-00 00:00:00' ? '' : data.fecha_limite ) : '',
        fechaTerminacion: data.fecha_terminacion ? (data.fecha_terminacion === '0000-00-00 00:00:00' ? '' : data.fecha_terminacion ) :'',
        tiempoAsignado: data.tiempo_asignado ? data.tiempo_asignado :'',
        ciudad: data.ciudad ? data.ciudad : [],
        supervisor: data.supervisor ? data.supervisor : [],
        incGastos: data.inc_gastos ? (data.inc_gastos === 1 ? true : false) : false,
        tarSinchi: data.tar_sinchi ? (data.tar_sinchi === 1 ? true : false) : false,
        valGastosViaje: data.val_gastos_viaje ? data.val_gastos_viaje : '',
        tipoProc: data.tipo_proc ? data.tipo_proc : '',
        necesidades: data.necesidades ? data.necesidades : [],
        pagos: data.pagos ? data.pagos : [],
        anticipo: data.anticipo ? data.anticipo : '',
        accesInfoConf: data.acces_info_conf ? (data.acces_info_conf === 1 ? true : false) : false,
        caracteristicasBS: data.caracteristicas_bs ? data.caracteristicas_bs : '',
        cod_causal: data.cod_causal ? data.cod_causal : '',
        causal: data.causal ? data.causal : '',
        contrapartida: data.contrapartida ? data.contrapartida : '',
        convDinero: data.conv_dinero ? data.conv_dinero : '',
        convEspecie: data.conv_especie ? data.conv_especie : '',
        convValor :data.conv_valor ? data.conv_valor : '',
        convenioMarco: data.convenio_marco ? data.convenio_marco : '',
        correo: data.correo ? data.correo : '',
        fechaCreacion: data.fecha_creacion ? data.fecha_creacion : '',
        justificacion: data.justificacion ? data.justificacion : '',
        nombre: data.nombre ? data.nombre : '',
        ofEconomica: data.of_economica ? data.of_economica : '',
        pesoEco: data.peso_eco ? (data.peso_eco === 0 ? '' : data.peso_eco) : '',
        pesoEqu: data.peso_equ ? (data.peso_equ === 0 ? '' : data.peso_equ) : '',
        pesoFin: data.peso_fin ? (data.peso_fin === 0 ? '' : data.peso_fin) : '',
        pesoMet: data.peso_met ? (data.peso_met === 0 ? '' : data.peso_met) : '',
        pesoOrg: data.peso_org ? (data.peso_org === 0 ? '' : data.peso_org) : '',
        pesoProp: data.peso_prop ? (data.peso_prop === 0 ? '' : data.peso_prop) : '',
        porcOfEco: data.porc_of_eco ? (data.porc_of_eco === 0 ? '' : data.porc_of_eco) : '',
        rol: data.rol ? data.rol : '',
        telefono: data.telefono ? data.telefono : '',
        tipoContratacion: data.tipo_contratacion ? data.tipo_contratacion : '',
        valArl: data.val_arl ? data.val_arl : '',
        educacion: data.educacion ? data.educacion : [],
        experiencia: data.experiencia ? data.experiencia : [],
        adjuntos: data.adjuntos ? this.adjMaped(data.adjuntos) : [],
        proponentes: (data.proponentes) ? this.propMapped(data.proponentes) : [],
        expProp: data.expProp ? this.expMaped(data.expProp) : [],
        roles: data.roles ? this.rolesMaped(data.roles) : [],
        expFin: data.expFin ? data.expFin : [],
        expOrg: data.expOrg ? data.expOrg : [],
        expMet: data.expMet ? data.expMet : [],
        criDes: data.criDes ? data.criDes : [],
        estado: data.estado ? data.estado : 'creado',
        cdp: data.cdp ? data.cdp : '',
        cdp_nombre: data.cdp_nombre ? data.cdp_nombre : '',
        cdp_number: data.cdp_number ? data.cdp_number : '',
        crp: data.crp ? data.crp : '',
        crp_nombre: data.crp_nombre ? data.crp_nombre : '',
        crp_number: data.crp_number ? data.crp_number : '',
        ab_asignado: data.ab_asignado ? data.ab_asignado : '',
        alcance: data.alcance ? data.alcance : '',
        id_plantilla: data.id_plantilla ? data.id_plantilla : '',
        cod_tipocontrata: data.cod_tipocontrata ? data.cod_tipocontrata : '',
        norma: data.norma ? data.norma : ''
      }
      this.termref = obj
    },
    propMapped (proponentes) {
      proponentes.map(item => {
        item.adjuntos = []
      })
      return proponentes
    },
    adjMaped (adjuntos) {
      console.log(adjuntos)
      adjuntos.map(item => {
        item.aprobado = item.aprobado ? (item.aprobado === 0 ? false : true) : false
        item.noAplica = item.noAplica ? (item.noAplica === 0 ? false : true) : false
      })
      return adjuntos
    },
    expMaped (exp) {
      exp.map(item => {
        item.cumple = item.cumple ? (item.cumple === 0 ? false : true) : false
      })
      return exp
    },
    rolesMaped (rol) {
      console.log(rol)
      rol.map(item => {
        item.educacion.map(element => {
          element.cumple = element.cumple ? (element.cumple === 0 ? false : true) : false
          element.noAplica = element.noAplica ? (element.noAplica === 0 ? false : true) : false
        })
      })
      return rol
    },
  },
  computed: {
    esDirecta () {
      return this.$store.state.tiposContratacionDirecta.includes(this.termref.tipoContratacion)
    }
  }
}
</script>
