<template>
  <v-card>
    <v-tabs
      v-model="tab"
      icons-and-text
      >
      <v-tabs-slider></v-tabs-slider>

      <v-tab href="#tab-1">
        Adjuntos
        <v-icon>mdi-phone</v-icon>
      </v-tab>

      <v-tab href="#tab-2">
        Criterios Educación
        <v-icon>mdi-heart</v-icon>
      </v-tab>

      <v-tab href="#tab-3">
        Criterios Eperiencia
        <v-icon>mdi-account-box</v-icon>
      </v-tab>
    </v-tabs>
    <v-tabs-items v-model="tab">
      <v-tab-item
        value="tab-1"
        >
        <v-card flat>
          <datosAdjuntos
            v-if="termref"
            :tipoContratacion="termref.tipo_contratacion"
            :adjuntosIn="termref.adjuntos"
            :idSolProc="termref.id"
            :causal="termref.cod_causal"
            :hideTitle="true"
            :evaluacion="true"/>
        </v-card>
      </v-tab-item>
      <v-tab-item
        value="tab-2"
        >
        <v-card flat>
          <v-card-text>
            <Educacion
              class="mt-3"
              v-if="termref.educacion ? (termref.educacion.length > 0) : false "
              :educacion="termref.educacion"
              @addEstudio="(edu) => termref.educacion.unshift(edu)"
              @delEstudio="(index) => termref.educacion.splice(index, 1)"
              :evaluacion="true"/>
          </v-card-text>
        </v-card>
      </v-tab-item>
      <v-tab-item
        value="tab-3"
        >
        <Experiencia
          v-if="termref.experiencia ? (termref.experiencia.length > 0) : false "
          class="mt-3" :experiencia="termref.experiencia"
          @add-experiencia="(exp) => termref.experiencia.unshift(exp)"
          @del-experiencia="(index) => termref.experiencia.splice(index, 1)"
          :evaluacion="true"/>
      </v-tab-item>
    </v-tabs-items>
    <v-row justify="end" class="my-5 pb-5" no-gutters>
      <v-btn color="primary" text class="mr-5">volver</v-btn>
      <v-btn color="primary" class="mr-5" @click="savechanges">guardar</v-btn>
    </v-row>
    <v-dialog
      v-model="dialogSaving"
      hide-overlay
      persistent
      width="300"
      >
      <v-card
        color="primary"
        dark
      >
        <v-card-text>
          {{textDialog}}
          <v-progress-linear
            indeterminate
            color="white"
            class="mb-0"
          ></v-progress-linear>
        </v-card-text>
      </v-card>
    </v-dialog>
    <!-- <pre>{{termref}}</pre> -->
  </v-card>  
</template>
<script>
import datosAdjuntos from '@/components/solProcContrata/datosAdjuntos'
import Educacion from '@/components/terminos_referencia/Educacion.vue'
import Experiencia from '@/components/terminos_referencia/Experiencia.vue'
import { termRef } from '@/servicios/TerminosReferencia.js'
export default {
  components: { datosAdjuntos, Educacion, Experiencia },
  props: ['esDirecta', 'termref'],
  data () {
    return {
      tab: 'tab-1',
      dialogSaving: false,
      textDialog: ''
    }
  },
  mounted () {},
  methods: {
    async savechanges () {
      this.textDialog = 'Guardando los cambios...'
      this.dialogSaving = true
      try {
        const data = { ...this.termref }
        console.log(data)
        const result = await termRef.updateTermRef(data)
        this.$toasted.success('Datos actualizados!', { icon: 'done', duration: 3000 })
        console.log(result)
      } catch (err) {
        this.$toasted.error(err.response.data, { icon: 'error', duration: 5000 })
        console.log(err.response.data)
      }
      this.dialogSaving = false
    }
  }
}
</script>

